export default {
	/**/
	showFooter: ({ commit }) => {
		commit(types.SHOW_FOOTER)
	},
	hideFooter: ({ commit }) => {
		commit(types.HIDDE_FOOTER);
	},
	//  
	showHeader:({ commit }) => {
			commit(types.SHOW_HEADER);
	},
	hideHeader:({ commit }) => {
		commit(types.HIDDE_HEADER);
	}
	

}


