//mapGetters  store  getters 
import {
    SHOW_FOOTER,// 
    HIDDE_FOOTER,
    SHOW_HEADER,// 
    HIDDE_HEADER
} from './mutation_types.js'

// 1 :    
const state = {
    showBottom: true, // 
    showHeader:true,  //       
}

const mutations = {
    /*footer*/
    [SHOW_FOOTER](state) {
        state.showBottom = true;
    },
    [HIDDE_FOOTER](state) {
        state.showBottom = false;
    },
    [SHOW_HEADER](state){
    	state.showHeader = true;
    },
     [HIDDE_HEADER](state){
    	state.showHeader = false;
    },
};

 // 2 :  
const getters = {
    // ...
    showBottom(state) {
        return state.showBottom
    },
    showHeader(state){
    	return state.showHeader
    }
}


export default {
    state,
    mutations,
    getters
}


